import { mapActions } from 'vuex'
import { upyunUrl } from '../../../../../api/config'

export default {
  data () {
    return {
      upyunUrl,
      oJson: '',
      obj: ''
    }
  },
  onLoad (options) {
    this.oJson = JSON.parse(decodeURIComponent(options.oJson))
  },
  onShow () {
    this.getConsultInfoFn()
  },
  // 下拉刷新
  onPullDownRefresh () {
    this.getConsultInfoFn()
  },
  methods: {
    ...mapActions([
      'getConsultInfo'
    ]),
    // 获取咨询信息
    getConsultInfoFn () {
      uni.showLoading({ title: 'Loading' })
      this.getConsultInfo({ that: this, id: this.oJson.id }).then((res) => {
        res['buy_type'] = 'consulting'
        this.obj = res
        uni.hideLoading()
        uni.stopPullDownRefresh()
      })
    },
    // 拨打电话
    onCallPhoneFn (phone) {
      uni.makePhoneCall({
        phoneNumber: phone
      })
    },
    // 复制文本
    onCopyTextFn (url) {
      uni.showModal({
        title: '复制成功',
        content: '微信号：' + url,
        confirmColor: '#e1564f',
        success () {
          uni.setClipboardData({
            data: url
          })
        }
      })
    },
    // 跳转页面
    onSkipPageFn (url) {
      uni.navigateTo({
        url: url + encodeURIComponent(JSON.stringify(this.obj))
      })
    }
  },
  onUnload () {
    this.obj = ''
  }
}
